.About {
    display: flex;
    margin: 40px 12vw;
    flex-direction: column-reverse;
    justify-content: center;
}

.Headshot {
    flex: 1;
    /* padding-right: 25vw; */
    padding-bottom: 100px;
}

.AboutImage {
    object-fit: scale-down;
    width: 100%;
}

.Bio {
    flex: 3;
    text-align: left;
    /* padding-right: 25vw; */
    color: var(--secondary-color);
    /* padding-bottom: 40px; */
}

.About p {
    font-size: 1.25em;
    padding: 0px;
}